<template>
	<div id="login_screen">
		<vue-element-loading 
			:active="isLoading"
			:background-color="'rgba(255, 255, 255, .2)'"
			:is-full-screen="true"
			:color="'blue'"
		/>
		<div class="login_screen__container">
			<div class="login_screen__logo">
				<img 
					class="login_screen__logo_img"
					src="@/assets/home/logo.png"
				>
			</div>
			<v-form
				ref="form"
				@submit.prevent="submitForm"
			>
				<div class="login_screen__form">
					<v-text-field
						v-model.trim="username"
						filled
						dark
						dense
						outlined
						required
						label="Login"
						:rules="generalRules"
					/>
					<v-text-field
						v-model.trim="password"
						dark
						filled
						dense
						outlined
						required
						label="Password"
						:rules="generalRules"
						:append-icon="showPassword ? 'fa-thin fa-eye' : 'fa-sharp fa-light fa-eye-slash'"
						:type="showPassword ? 'text' : 'password'"
						class="password-input"
						@click:append="showPassword = !showPassword"
					/>
					<div class="remember_me">
						<v-checkbox
							v-model="rememberMe" 
							dark
							hide-details
						/>
						<span class="checkbox_label">Remember me</span>
					</div>
					<div class="login_screen__buttons">
						<v-btn
							class="login_screen__buttons_submit"
							elevation="5"
							@click="submit"
						>
							Login
						</v-btn>
					</div>
				</div>
			</v-form>
		</div>
	</div>
</template>

<script>
import { getCookie, setCookie } from '@/helpers/cookies.js';
import { HTTP } from '@/helpers/httpCommon';
import dayjs from 'dayjs';

export default { 
	data() {
		return {
			generalRules: [
				v => !!v || '*Обязательное поле',
			],
			username: '',
			password: '',
			showPassword: false,
			rememberMe: false,
			isLoading: false,
			
		};
	},
	created() {
		const rememberMe = localStorage.getItem('remember_me');
		const isLoggedIn = getCookie('user_state');
		if(rememberMe) this.rememberMe = JSON.parse(rememberMe);
		if(isLoggedIn) this.$router.push({ name: 'Home' }).catch(() => {});
	},
	mounted() {
		if(this.rememberMe) {
			const userData = getCookie('user_data');
			if(userData) this.username = JSON.parse(userData).username || '';
		}
	},
	methods: {
		async submit() {
			localStorage.setItem('remember_me', this.rememberMe);
			this.showErrorAlert = !this.showErrorAlert;
			if(this.validate()) {
				this.isLoading = true;
				HTTP.post('user/login', {
					'username': this.username,
					'password': this.password
				}).then((response) => {
					const expires = dayjs().add(7, 'days').toISOString();
					setCookie('_token', response.data.Data, { expires: expires });
					setCookie('user_state', 'logged_in', { expires: expires });
					if(this.rememberMe) {
						setCookie('user_data', JSON.stringify({ username: this.username }), { expires: expires });
					}
					this.$router.push({ name: 'Home' }).catch(() => {});
				}).catch((error) => {
					this.$toast.error(error.response.data.ResponseMessage, {
						timeout: 2000
					});
				}).finally(() => {
					this.isLoading = false;
				});
			}
		},
		validate () {
			return this.$refs.form.validate();
		},
		reset () {
			this.$refs.form.reset();
		},
	}
};
</script>

<style lang="scss" scoped>
	#login_screen {
		box-sizing: border-box;
		display: flex;
		flex-direction: column;
		justify-content: center;

		width: 100%;
		height: 100vh;
		background-image: url('@/assets/home/bg.png');
		background-size: cover;
	}
	.login_screen {
		&__container {
			width: 50%;
			display: flex;
			flex-direction: column;
			justify-content: center;
			height: 100%;
			margin: 0 auto;
		}
		@media (max-width: 1024px) {
			&__container {
				width: 60%;
			}
		}
		@media (max-width: 768px) {
			&__container {
				width: 90%;
			}
		}
		&__logo {
			display: flex;
			justify-content: center;
			margin-bottom: 5rem;
			&_img {
				width: 136px;
				height: 92px;
			}
		}
		&__buttons {
			margin-top: 3rem;
			display: flex;
			justify-content: center;
			&_submit {
				width: 50%;
			}
		}
	}
	.remember_me {
		display: flex;
		align-items: center;
		padding: 0;
	}
	.checkbox_label {
		margin-left: 7px;

		color: rgba(255, 255, 255, 0.707);
		font-size: 12px;
	}
	.remember_me::v-deep .v-input {
		transform: scale(0.8);
		margin: 0;
		.v-input--selection-controls__input {
			margin: 0;
		}
	}
	.password-input::v-deep {
		.v-icon {
			font-size: 16px;
		}
	}
	.btn_size {
		width: 150px
	}
</style>